import React from 'react';
import Immutable from 'immutable';
import { Q, Grade } from 'js/quantum';
import $ from 'jquery';
import { Provider } from 'react-redux'
import { ItemTypeDesc } from 'js/subjects';
import { ItemDisplay } from 'js/item/display';
import ProvinceSelect from 'js/widgets/province_select';
import { uploadFile } from 'js/widgets/uploader';
import { ItemSearchPanel } from 'js/item/search';
import  ItemParsing from 'component/ItemParsing';
import { setCurrentNav } from 'js/paper/common';
import { Modal, ModalHeader, ModalBody, ModalFooter, ModalSingle,Modalbig} from 'js/widgets/modal';
import "css/main.scss";
import { store } from "js/main_entry";

export class OmegaPaper extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            paper: null,
            ajaxing: false,
            edu:Global.user.edu
        };
    }
    
    componentWillMount(){
    	Q.get(`/api/user/${Global.userId}`)
        .done((res) => {
        	this.setState({
	        		edu:res.edu
	        	},()=>{
	        		Global.user.edu=res.edu;
	        	})
	        })
	    }

    componentDidMount() {
        Q.get(`/api/omega_paper/${this.props.params.id}`)
        .done((paper) => { this.setState({ paper: Immutable.fromJS(paper) }); })
        ;
        setCurrentNav('试卷');
    }

    onPropChanged(name, value) {
        this.setState(
            { paper: this.state.paper.set(name, value) }
        );
    }

    save() {
        const json = {
            action: 'update',
            data: this.state.paper.toObject(),
        };
        this.setState({ ajaxing: true });
        Q.post(`/api/omega_paper/${this.props.params.id}`, { json })
            .done((paper) => { location.href = `/omega_paper/${paper._id}`; })
            .always(() => { this.setState({ ajaxing: false }); })
        ;
    }

    remove() {
        if (!confirm('确认删除此试卷?')) {
            return;
        }
        this.setState({ ajaxing: true });
        Q.delete_(`/api/omega_paper/${this.props.params.id}`)
            .done((paper) => { location.href = `/omega_paper/${paper._id}`; })
            .always(() => { this.setState({ ajaxing: false }); })
        ;
    }

    addItem(item) {
    	item.map((v,i)=>{
    		if (this.state.paper.get('item_ids').includes(v)) {
	            Q.alert('此题已添加', 'warning');
	            item.splice(i,1);
	        }

	    	this.setState({
	        	paper: this.state.paper.updateIn(['item_ids'], list => list.concat(item))
	        });
    		$('#modal-container .bs-example-modal-lg').modal('hide');
    	})
    }
    
    addItemSingle(item) {
    	
		if (this.state.paper.get('item_ids').includes(item)) {
            Q.alert('此题已添加', 'warning');
            return;
        }
        $('#modal-container .bs-example-modal-lg').modal('hide');
        this.setState(
            { paper: this.state.paper.updateIn(['item_ids'], list => list.push(item)) }
        );
    }
    
    
    showSearchModal() {
    	localStorage.removeItem('idArr');
    	localStorage.removeItem('dataSel');
    	localStorage.setItem('dataSel',JSON.stringify([]))
        const modal = Modalbig.show(
            <Provider store={store}>
                <Modalbig modalDialogClass="modal-big" edu={this.state.edu} callbackBig={()=>this.addItem( JSON.parse(localStorage.getItem('idArr')) )}>
                    {this.props.children}
                </Modalbig>
            </Provider>
        );
    }

    showCreateModal() {
        const modal = Modal.show(
            <Provider store={store}>
                <ModalSingle modalDialogClass="modal-big" edu={this.state.edu} saveCallback={(item) => {this.addItemSingle(item);}} edu={Global.user.edu}>
            </ModalSingle>
            </Provider>

        );

    }

    render() {
        if (this.state.paper === null) {
            return (<div className="loading">加载中...</div>);
        }
        const opable = !this.state.ajaxing;
        return (
            <div id="paper-view">
                <PaperHeader
                    paper={this.state.paper}
                    key="header"
                    onPropChanged={this.onPropChanged.bind(this)}
                    onCreateItem={this.showCreateModal.bind(this)}
                    onSearchItem={this.showSearchModal.bind(this)}
                />
                <hr />
                <PaperBody
                    itemIds={this.state.paper.get('item_ids')}
                    itemTpls={this.state.paper.get('item_tpls')}
                />
                <div className="op-zone paper-op-zone panel panel-default">
                    <button
                        className="btn btn-danger" disabled={!opable}
                        onClick={this.remove.bind(this)}
                    >删除</button>
                    <button
                        className="btn btn-primary" disabled={!opable}
                        onClick={this.save.bind(this)}
                    >保存</button>
                </div>
            </div>
        );
    }
}

function PaperHeader(props) {
    return (
        <div className="paper-header">
            <div className="paper-item-ids">
                <h3>题目ID列表</h3>
                <TextList
                    items={props.paper.get('item_ids')}
                    onListChanged={(newList) => { props.onPropChanged('item_ids', newList); }}
                />
            </div>
            <div className="paper-metas">
                <h3>试卷基本信息</h3>
                <PaperMetaForm {...props} />
            </div>
        </div>
    );
}

//function PaperMetaForm(props) {
class PaperMetaForm extends React.Component {
	
	redirect(reviewProps){
		Q.get(`/item/review?review=${reviewProps}&paper_id=${this.props.paper.get('_id')}`).done(data => {
	        if (data) {
	            window.location.href = data;
	        }
	    })
	}	
	    
	
	render(){
		return (
	        <div className="form-horizontal">
	            <div className="form-group">
	                <label className="col-sm-1 control-label">名字</label>
	                <div className="col-sm-11">
	                    <input
	                        type="text" value={this.props.paper.get('name')} required
	                        onChange={(event) => {
	                            this.props.onPropChanged('name', event.target.value);
	                        }}
	                        className="form-control"
	                    />
	                </div>
	            </div>
	            <div className="form-group">
	                <label className="col-sm-1 control-label">年份</label>
	                <div className="col-sm-11">
	                    <input
	                        type="number" value={this.props.paper.get('year')} required
	                        onChange={(event) => {
	                            this.props.onPropChanged('year', parseInt(event.target.value, 10));
	                        }}
	                        className="form-control"
	                    />
	                </div>
	            </div>
	            <div className="form-group">
	                <label className="col-sm-1 control-label">省份</label>
	                <div className="col-sm-5">
	                    <ProvinceSelect
	                        value={this.props.paper.get('districts')}
	                        onListChanged={(newList) => {
	                            this.props.onPropChanged('districts', newList);
	                        }}
	                        className="form-control province-select"
	                    />
	                </div>
	            </div>
	            <div className="form-group">
	                <label className="col-sm-1 control-label">年级</label>
	                <div className="col-sm-11">
	                    <select
	                        name="grade" className="form-control" value={this.props.paper.get('grade')}
	                        onChange={event =>
	                            this.props.onPropChanged('grade', parseInt(event.target.value, 10))
	                        }
	                    >
	                        {
	                            Grade.kAll.map(gradeDesc =>
	                                <option value={gradeDesc.grade} key={gradeDesc.grade}>
	                                    {gradeDesc.name}
	                                </option>
	                            )
	                        }
	                    </select>
	                </div>
	            </div>
	            <div className="form-group">
	                <label className="col-sm-1 control-label">类别</label>
	                <div className="col-sm-11">
	                    <input
	                        type="text" value={this.props.paper.get('cat')} required
	                        onChange={event => this.props.onPropChanged('cat', event.target.value)}
	                        className="form-control"
	                    />
	                </div>
	            </div>
	            <div className="form-group">
	                <label className="col-sm-1 control-label">Doc</label>
	                <div className="col-sm-9">
	                    <input
	                        type="string" value={this.props.paper.get('doc')} readOnly
	                        className="form-control"
	                    />
	                </div>
	                <div className="col-sm-2">
	                    <a
	                        target='_blank'
	                        href={this.props.paper.get('doc') ?
	                            Q.data_file_url(
	                                'doc', this.props.paper.get('doc'),
	                                `${this.props.paper.get('name')}.pdf`)
	                            : '#'}
	                        disabled={!this.props.paper.get('doc')}
	                    > 下载 </a> &nbsp;&nbsp;
	                    <button
	                        className="btn btn-default"
	                        onClick={() => uploadFile(
	                            '上传Doc',
	                            json => this.props.onPropChanged('doc', json.filename),
	                            { accept: 'application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document' }
	                        )}
	                    >上传</button>
	                </div>
	            </div>
	            <div className="form-group">
	                <label className="col-sm-1 control-label">操作</label>
	                <div className="col-sm-9">
	                    <button
	                        className="btn btn-success btn-sm"
	                        onClick={() => this.props.onSearchItem()}
	                    ><span className="glyphicon glyphicon-search" aria-hidden="true" />
	                    查询添加</button>
	                    <button
	                        className="btn btn-success btn-sm"
	                    ><span className="glyphicon glyphicon-search" aria-hidden="true" />
	                        <a onClick={()=>this.redirect('tag')}>标注</a></button>
	                    <button
	                        className="btn btn-success btn-sm"
	                    ><span className="glyphicon glyphicon-search" aria-hidden="true" />
	                        <a onClick={()=>this.redirect('typeset')}>审核</a></button>
	                    <button
	                        className="btn btn-success btn-sm col-sm-offset-1"
	                        onClick={() => this.props.onCreateItem()}
	                    ><span className="glyphicon glyphicon-plus" aria-hidden="true" />
	                    新建题目</button>
	                    {this.props.paper.get('volume_id') &&
	                        <a
	                            href={`/volume/${this.props.paper.get('volume_id')}`}
	                            target="_blank" className="col-sm-offset-1"
	                        >查看录入</a>
	                    }
	                </div>
	            </div>
	            <div className="form-group">
	                <label className="col-sm-1 control-label">Deleted</label>
	                <div className="col-sm-2">
	                    <p className="form-control-static">
	                        {this.props.paper.get('deleted') ? 'YES' : 'NO' }
	                    </p>
	                </div>
	            </div>
	        </div>
	    );
	}
    
}

function getNonEmptyLines(val) {
    return val.split(/\n/).map(s => s.trim()).filter(s => !!s);
}

class TextList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: this.props.items.join('\n'),
        };
    }

    componentWillReceiveProps(nextProps) {
        this.setState({ value: nextProps.items.join('\n') });
    }

    handleBlur() {
        const lines = getNonEmptyLines(this.state.value);
        this.props.onListChanged(Immutable.List(lines));
    }

    render() {
        return (
            <textarea
                cols="30" rows="25" value={this.state.value} className="form-control code"
                onChange={(event) => { this.setState({ value: event.target.value }); }}
                onBlur={this.handleBlur.bind(this)}
            />
        );
    }
}

function PaperBody(props) {
    const trs = props.itemIds.map((itemId, index) => {
        return (
            <PaperItem
                key={itemId} itemId={itemId} no={index + 1}
            />
        );
    });
    return (
        <div className="paper-body">
            <h2>题目部分</h2>
            <table className="paper-items table table-striped">
                <tbody>
                    {trs}
                </tbody>
            </table>
        </div>
    );
}

class PaperItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            itemTypeName: '(未知)',
        };
    }

    onItemLoaded(itemDisplay) {
        if (itemDisplay.state.error) {
            return;
        }
        const item = itemDisplay.state.item;
        this.setState({ itemTypeName: ItemTypeDesc.get(item.get('data').get('type')).name });
    }

    render() {
        return (
            <tr>
                <td width="30%">
                    <ul>
                        <li>序号：{this.props.no}</li>
                        <li>ID：
                            <a href={`/item/${this.props.itemId}`} target='_blank' className="code">
                                {this.props.itemId}
                            </a>
                        </li>
                        <li>题型: {this.state.itemTypeName}</li>
                    </ul>
                </td>
                <td width="70%">
                    <ItemDisplay id={this.props.itemId} onLoaded={this.onItemLoaded.bind(this)} />
                </td>
            </tr>
        );
    }
}



