import React from 'react';
import {Edu, Q} from "js/quantum";
import {getSubject} from "js/subjects";
import {HtmlWithTex} from 'js/widgets/html_with_tex';
import {EduDesc} from "js/quantum";
import {ItemClassDesc} from "js/subjects";
import { Modal, ModalHeader, ModalBody, ModalFooter,ModalCreateTrue,ModalSingle} from 'js/widgets/modal';
import "css/itemParsing.scss"
import FormulaKit from './formulaKit'
export class ItemTypeSwitcher extends React.Component {
	constructor(props) {
        super(props);
        this.state = {
            value: this.props.edu2?ItemClassDesc.defaultForEdu(this.props.edu2).class_:null,
            edu2:3
        };
    }
	componentWillReceiveProps(nextProps) {
        if (nextProps.edu !== this.props.edu) {
            this.setState({value: ItemClassDesc.defaultForEdu(nextProps.edu).class_});
        }
    }
	
	componentDidMount(){
    	if(this.props.edu2){
    		this.setState({
	    		edu2:this.props.edu2
	    	})
    	}else{
    		this.setState({
	    		edu2:this.props.eduYL
	    	})
    	}
    }
	
    handleClick=(ev) =>{
    	if(this.props.edu2){
    		this.setState({
	    		value:+ev.target.value,
	    		edu2:this.props.edu2
	    	})
    	}else{
    		this.setState({
	    		value:+ev.target.value,
	    		edu2:Global.user.edu
	    	})
    	}
    	if (ev.target.value === this.props.type) {
            return;
        }
        this.props.onTypeChanged(+ev.target.value);
    }
    

    render() {
	    let btns = [] ;
    	if(this.props.edu){
    		btns = ItemClassDesc.allForEduTestType(this.props.edu,this.props.type).map(
	//      const btns = getSubject().getItemTypeDescs(this.props.edu).map(
	            typeDesc => 
	                <option
	                 	key={typeDesc.type}
	                 	value={typeDesc.type}
	                    className={(typeDesc.type === this.props.type ? 'active' : '')}
	                    
	                >{typeDesc.name}
	                </option>
	        );

    	}else{
    		btns = ItemClassDesc.allForEduTestType(this.props.eduYL,this.props.type).map(
	            typeDesc => 
	                <option
	                 	key={typeDesc.type}
	                 	value={typeDesc.type}
	                    className={(typeDesc.type === this.props.type ? 'active' : '')}
	                >{typeDesc.name}
	                </option>
	        );
    	}
        return (
            <div className="ulBox">
                <span className="ulName">题型</span>
                <span className="ulAloneBox">
		            	<select value={this.state.value} onChange={this.handleClick} id="Flag2" className="liBox btn-group" >{btns}</select>
	            	</span>
            </div>
        );

    }
}


