import React from 'react'
import ReactDOM from 'react-dom'
export default class Portal extends React.Component {
    constructor(props) {
        super(props)
        this.el = document.createElement('div');
        this.el.className = 'plan-preview-background';
        let container = document.createElement('div');
        let button = document.createElement('button');
        button.innerHTML = '<span aria-hidden="true">&times;</span>';
        button.className = 'close';
        button.setAttribute('type', 'button');
        button.addEventListener('click', this.closePreview);
        container.appendChild(button);
        this.el.appendChild(container);
        container.className = 'plan-preview'
        if (this.props.previewPlan) {
            this.el.className = 'plan-preview-background plan-preview-show'
        }

    }

    componentDidMount() {
        document.body.appendChild(this.el)
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.previewPlan) {
            this.el.classList.add('plan-preview-show')
        } else {
            this.el.classList.remove('plan-preview-show')
        }
    }
    closePreview = ()=> {
        this.el.classList.remove('plan-preview-show')
        this.props.closePortalCallback && this.props.closePortalCallback();
    }
    componentWillUnmount() {
        document.body.removeChild(this.el)
    }

    render() {
        return ReactDOM.createPortal(this.props.children, this.el.querySelector(".plan-preview"))
    }
}



