import React from 'react';
import Immutable from 'immutable';
import { Q, Grade, DEFAULT_PROVINCE_LIST } from 'js/quantum';
import { setCurrentNav } from 'js/paper/common';


export class OmegaPaperList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            papers: [],
        };
    }

    componentDidMount() {
        setCurrentNav('试卷');
        console.log('加载完后：'+this.state.papers)
    }

    onFilterChange(query) {
        Q.ajax('/api/omega_papers',
               { query })
        .done((json) => { this.setState({ papers: Immutable.fromJS(json)}); },()=>{console.log(this.state.papers)})
        ;
        
    }

    render() {
        return (
            <div>
                <PaperListFilter onFilterChange={this.onFilterChange.bind(this)} />
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th width="5%"> 序号 </th>
                            <th width="30%"> 名称 </th>
                            <th width="10%"> 时间 </th>
                            <th width="15%"> 地区 </th>
                            <th width="10%"> 年级 </th>
                            <th width="10%"> 分类 </th>
                            <th width="20%"> ID </th>
                        </tr>
                    </thead>
                    <tbody>
                        { this.state.papers.map((paper, index) =>
                            <PaperRow paper={paper} index={index} key={index} />) }
                    </tbody>
                </table>
                <hr />
                <PaperCreateForm {...this.props} />
            </div>
        );
    }
}

class PaperListFilter extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            year: 2016,
            grade: 0,
            district: '',
            cat: '',
            filters: [],
        };
    }

    componentDidMount() {
        Q.get('/api/omega_papers/filters')
            .done((results) => {
                const filters = results;
                filters.push({
                    name: 'grade',
                    values: [0].concat(Grade.kAll.map(desc => desc.grade)),
                });
                filters.push({
                    name: 'district',
                    values: [''].concat(DEFAULT_PROVINCE_LIST),
                });
                this.setState({ filters });
            })
        ;
    }

    shouldComponentUpdate(nextProps, nextState) {
        return Object.keys(this.state).some(key => this.state[key] !== nextState[key]);
    }

    componentDidUpdate() {
        this.props.onFilterChange({
            year: this.state.year,
            grade: this.state.grade,
            district: this.state.district,
            cat: this.state.cat,
        });
    }

    handleChange(evt) {
        let value = evt.target.value;
        if (evt.target.name === 'year' || evt.target.name === 'grade') {
            value = parseInt(value, 10);
        }
        this.setState({ [evt.target.name]: value });
    }

    _renderFilters() {
        return this.state.filters.map((filter) => {
            switch (filter.name) {
            case 'year': return this._renderFilterYear(filter);
            case 'cat': return this._renderFilterCat(filter);
            case 'grade': return this._renderFilterGrade(filter);
            case 'district': return this._renderFilterDistrict(filter);
            default: return null;
            }
        });
    }

    _renderFilterYear(filter) {
        return (
            <div className="col-xs-2" key={filter.name}>
                <select
                    className="form-control" name={filter.name} value={this.state.year}
                    onChange={this.handleChange.bind(this)}
                >
                    { filter.values.map(year =>
                        <option value={year} key={year}>
                            {year === 0 ? '所有年份' : `${year}年`}
                        </option>
                    )}
                </select>
            </div>
        );
    }

    _renderFilterCat(filter) {
        return (
            <div className="col-xs-2" key={filter.name}>
                <select
                    className="form-control" name={filter.name} value={this.state.cat}
                    onChange={this.handleChange.bind(this)}
                >
                    { filter.values.map(cat =>
                        <option value={cat} key={cat}>{cat === '' ? '不限' : cat}</option>
                    )}
                </select>
            </div>
        );
    }

    _renderFilterGrade(filter) {
        return (
            <div className="col-xs-2" key={filter.name}>
                <select
                    className="form-control" name={filter.name} value={this.state.grade}
                    onChange={this.handleChange.bind(this)}
                >
                    { filter.values.map(grade =>
                        <option value={grade} key={grade}>
                            {grade === 0 ? '不限年级' : Grade.getGradeName(grade)}
                        </option>
                    )}
                </select>
            </div>
        );
    }

    _renderFilterDistrict(filter) {
        return (
            <div className="col-xs-2" key={filter.name}>
                <select
                    className="form-control" name={filter.name} value={this.state.district}
                    onChange={this.handleChange.bind(this)}
                >
                    { filter.values.map(district =>
                        <option value={district} key={district}>
                            {district === '' ? '不限地区' : district}
                        </option>
                    )}
                </select>
            </div>
        );
    }

    render() {
        return (
            <form className="form paper-filters" style={{ height: 40 }}>
                { this._renderFilters() }
            </form>
        );
    }
}

export default OmegaPaperList;

class PaperCreateForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            year: new Date().getFullYear(),
            grade: Grade.kSenior_3,
            cat: '',
        };
    }

    submit(event) {
        event.preventDefault();
        Q.put('/api/omega_papers', { json: this.state })
            .done((paper) => {
                location.href = `/omega_paper/${paper._id}`;
            })
        ;
    }

    render() {
        return (
            <form className="form row" onSubmit={this.submit.bind(this)}>
                <fieldset>
                    <legend>创建试卷</legend>

                    <div className="col-xs-3">
                        <div className="form-group">
                            <div className="input-group">
                                <span className="input-group-addon">名字</span>
                                <input
                                    type="text" value={this.state.name} name="name"
                                    placeholder="paper name" required className="form-control"
                                    onChange={event => this.setState({ name: event.target.value })}
                                />
                            </div>
                        </div>
                    </div>

                    <div className="col-xs-2">
                        <div className="form-group">
                            <div className="input-group">
                                <span className="input-group-addon">时间</span>
                                <input
                                    type="number" value={this.state.year} name="year"
                                    min="2009" max={`${new Date().getFullYear()}`} required className="form-control"
                                    onChange={event =>
                                        this.setState({ year: parseInt(event.target.value, 10) })
                                    }
                                />
                                <span className="input-group-addon">年</span>
                            </div>
                        </div>
                    </div>
                    <div className="col-xs-2">
                        <div className="form-group">
                            <div className="input-group">
                                <span className="input-group-addon">年级</span>
                                <select
                                    name="grade" className="form-control" value={this.state.grade}
                                    onChange={event =>
                                        this.setState({ grade: parseInt(event.target.value, 10) })
                                    }
                                >
                                    {
                                        Grade.kAll.map(gradeDesc =>
                                            <option value={gradeDesc.grade} key={gradeDesc.grade}>
                                                {gradeDesc.name}
                                            </option>
                                        )
                                    }
                                </select>
                            </div>
                        </div>
                    </div>
                    <div className="col-xs-2">
                        <div className="form-group">
                            <div className="input-group">
                                <span className="input-group-addon">类别</span>
                                <input
                                    type="text" value={this.state.cat} name="cat"
                                    placeholder="高考真题（理）"
                                    required className="form-control"
                                    onChange={event => this.setState({ cat: event.target.value })}
                                />
                            </div>
                        </div>
                    </div>
                    <div className="col-xs-1">
                        <div className="form-group">
                            <input type="submit" name="创建" value="创建" className="btn btn-primary" />
                        </div>
                    </div>
                </fieldset>
            </form>
        );
    }
}

function PaperRow(props) {
    return (
        <tr data-id={props.paper.get('_id')} >
            <td> {props.index + 1} </td>
            <td>
                <a href={`/omega_paper/${props.paper.get('_id')}`}>{props.paper.get('name')}</a>
            </td>
            <td> {props.paper.get('year')} </td>
            <td> {props.paper.get('districts').join(',')} </td>
            <td> {Grade.getGradeName(props.paper.get('grade'))} </td>
            <td> {props.paper.get('cat')} </td>
            <td className="code"> {props.paper.get('_id')} </td>
        </tr>
    );
}



